<template>
  <div class="settlement">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow @click-left="onClickLeft" />

    <div class="main">
      <!-- 选择收货地址 -->
      <div class="choice-address">
        <van-cell @click="showAddress = true" :title="selectAddress" is-link />
      </div>
      <!-- 订单 -->
      <div class="order">
        <div class="order-item" v-for="(item,index) in goodsData" :key="index">
          <div class="img" :style="{backgroundImage:'url('+ item.small_img + ')'}"></div>
          <div class="content">
            <p class="title">{{item.name}}</p>
            <p class="rule">{{item.rule}}</p>
          </div>
          <div class="count">x{{item.count}}</div>
          <div class="price">￥{{item.price * item.count}}</div>
        </div>

        <!-- 优惠卷 -->
        <van-coupon-cell
          class="juan"
          :coupons="coupons"
          :chosen-coupon="chosenCoupon"
          @click="showList = true"
        />
        <div class="order-total">
          总{{total}}件商品 合计:
          <span class="price">￥{{totalPrice}}</span>
        </div>
      </div>
    </div>

    <!-- 优惠卷列表 -->
    <van-popup v-model="showList" round position="bottom" style="height: 90%; padding-top: 4px;">
      <van-coupon-list
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        @change="onChange"
        @exchange="onExchange"
      />
    </van-popup>

    <!-- 收货地址列表 -->
    <van-popup
      class="address"
      v-model="showAddress"
      round
      position="bottom"
      style="height: 90%; padding-top: 4px;"
    >
      <div v-if="list.length == 0" class="no-address">暂无地址</div>
      <van-address-list
        @click-item="selectAdd"
        @add="onAdd"
        @edit="onEdit"
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
      />
    </van-popup>

    <!-- 提交订单 -->
    <div class="foot">
      <div class="submit">
        <div>
          应付:
          <span class="price">￥{{totalPrice}}</span>
        </div>
        <van-button @click="toPost" class="btn" type="default">结算</van-button>
      </div>
    </div>
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 150,
  name: "优惠券名称",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "1.5",
  unitDesc: "元"
};
import { mapState, mapGetters } from "vuex";
import Util from "../util/utils";
import Api from "../api/api";

export default {
  data() {
    return {
      // 展示地址
      showAddress: false,
      // 展示优惠卷
      showList: false,

      chosenCoupon: -1,
      coupons: [coupon],

      // 选择的地址ID
      chosenAddressId: "0"
    };
  },
  created() {
    // 先进行置空
    this.$store.commit("settlementModule/emptyData");

    // 根据地址渲染
    // 第一步从后台获取当前用户的地址数据
    let tokenString = Util.getCookies(this);

    Util.showLoading(this);
    // 渲染地址的
    Api.findAddress({ tokenString }).then(res => {
      if (res.data.code == 20000) {
        this.$toast(res.data.msg);

        // 把查询到的信息处理并初始化
        let data = res.data.result;

        data.forEach(v => {
          v.address = v.province + v.city + v.county + v.addressDetail;
          // 初始化默认ID
          if (v.isDefault) {
            this.chosenAddressId = v.id;
            // 初始化选择地址
            this.$store.commit("settlementModule/changeData", {
              key: "selectAddress",
              value: v.address
            });

            // 初始化字段信息
            this.$store.commit("settlementModule/changeData", {
              key: "orderInfo",
              value: {
                phone: v.tel,
                address: v.address,
                receiver: v.name
              }
            });
          }
        });

        // 初始化字段
        this.$store.commit("settlementModule/changeData", {
          key: "list",
          value: data
        });
      }
    });

    // 渲染商品的
    let sids = this.$route.query.sids;

    Api.commitShopcart({ tokenString, sids }).then(res => {
      if (res.data.code == 50000) {
        this.$toast(res.data.msg);

        let result = res.data.result; //拿好数组
        this.$store.commit("settlementModule/changeData", {
          key: "goodsData",
          value: result
        });
      }
    });
  },
  computed: {
    ...mapGetters("settlementModule", ["total", "totalPrice"]),
    ...mapState("settlementModule", [
      "list",
      "selectAddress",
      "goodsData",
      "orderInfo"
    ])
  },
  methods: {
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      this.coupons.push(coupon);
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    selectAdd(item) {
      // 改变地址

      this.$store.commit("settlementModule/changeData", {
        key: "selectAddress",
        value: item.address
      });

      this.$store.commit("settlementModule/changeData", {
        key: "orderInfo",
        value: {
          phone: item.tel,
          address: item.address,
          receiver: item.name
        }
      });

      this.showAddress = !this.showAddress;
    },
    onAdd() {
      // 新建直接去编辑页面
      this.$router.push({ name: "Editaddress" });
    },
    onEdit(item) {
      // 编辑就去带aid参数过去
      this.$router.push({ name: "Editaddress", params: { aid: item.aid } });
    },
    toPost() {
      // 结算按钮

      // 当没有地址的时候拦截
      if (this.selectAddress == "选择收货地址") {
        this.$toast("请先选择收货地址！");
        return;
      }

      let tokenString = Util.getCookies(this);
      let sids = this.$route.query.sids;
      Util.showLoading(this);
      Api.pay({ tokenString, sids, ...this.orderInfo }).then(res => {
        if (res.data.code == 60000) {
          this.$toast(res.data.msg);

          // 跳转post页面
          this.$router.push({ name: "Post" });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/util.scss";
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}
.no-address {
  text-align: center;
  height: 500px;
  line-height: 500px;
}

/deep/ .van-address-list {
  overflow-y: auto;
}

.juan {
  padding: 10px 0;
}
.choice-address {
  /deep/ .van-cell__title {
    @include ellipseOne();
  }
  .van-cell {
    border-radius: 10px;
  }
  margin: 10px auto;
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 0 1px 1px #ddd;
  font-size: 18px;
}
.settlement {
  height: 100%;
}
.main {
  height: calc(100% - 118px);
  overflow-y: auto;
}

.order {
  margin: auto;
  width: 300px;

  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: 0 0 1px 1px #ddd;
  padding: 10px 16px;
  .order-item {
    display: flex;
    height: 50px;
    margin-bottom: 10px;
    .img {
      padding: 25px;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  .content {
    padding-left: 10px;
    width: 130px;
    .title {
      font-size: 16px;
      height: 30px;
      line-height: 30px;
      font-weight: 600;
    }
    .rule {
      height: 20px;
      line-height: 20px;
      color: #999;
    }
  }
  .count {
    font-size: 14px;
    color: #999;
    width: 30px;
    height: 50px;
    line-height: 50px;
  }
  .price {
    font-size: 16px;
    text-align: right;
    width: 58px;
    height: 50px;
    line-height: 50px;
  }
}
.order-total {
  height: 40px;
  line-height: 40px;
  text-align: right;
  color: #999;
  .price {
    color: #000;
    font-weight: 600;
  }
  border-bottom: 1px solid #eee;
}

.submit {
  position: absolute;
  bottom: 10px;
  padding: 0 10px;
  left: 0;
  right: 0;
  margin: auto;
  width: 300px;
  height: 50px;
  line-height: 50px;
  box-shadow: 0 0 1px 1px #ddd;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .price {
    font-size: 20px;
    font-weight: 600;
  }
  .btn {
    background: #7f5839;
    color: #fff;
    border-radius: 10px 20px;
  }
}
.foot {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 62px;
  z-index: 5;
  background-color: #fff;
}

// 重设样式
.tig {
  width: 100%;
  font-size: 22px;
  height: 500px;
  text-align: center;
  line-height: 500px;
}

.address {
  /deep/ .van-button--danger {
    background-color: #7f5839;
    border: 1px solid #7f5839;
  }
  /deep/ .van-address-item .van-radio__icon--checked .van-icon {
    background-color: #7f5839;
    border-color: #7f5839;
  }
  /deep/ .van-address-item {
    border: 1px solid #7f5839;
  }
}
</style>